	module.exports = {"data":{"markdownRemark":{"html":"<h2>Overfitting Has Many Faces</h2>\n<p>If a learning algorithm fits a given training set well, this does not simply indicate a good hypothesis. Overfitting occurs when the hypothesis function J(Θ) fits your training set too closely having a high variance and low error on the training set while having a high test error on any other data. </p>\n<p>In other words, overfitting occrus if the error of the hypothesis as measured on the data set that was used to train the parameters happens to be lower than the error on any other data set.</p>\n<h3>Choosing an Optimal Polynomial Degree</h3>\n<p>Choosing the right degree of polynomial for the hypothesis function is important in avoiding overfitting. This can be achieved by testing each degree of polynomial and observing the effect on the error result over various parts of the data set. Hence, we can break down our data set into 3 parts that can be used in optimizing the hypothesis’ theta and polynomial degree. </p>\n<p>A good break-down ratio of the data set is: </p>\n<ul>\n<li>Training set: 60%</li>\n<li>Cross validation: 20%</li>\n<li>Test set: 20%</li>\n</ul>\n<p>The three error values can thus be calculatted by the following method:<sup>1</sup></p>\n<ol>\n<li>Use the training set for each polynomial degree in order to optimize the parameters in <code>Θ</code></li>\n<li>Use the cross validation set to find the polynomial degree with the lowest error</li>\n<li>Use the test set to estimate the generalization error </li>\n</ol>\n<h3>Ways to Fix Overfitting</h3>\n<p>These are some of the ways to address overfitting:</p>\n<ol>\n<li>Getting more training examples</li>\n<li>Trying a smaller set of features</li>\n<li>Increasing the parameter <code>λ lambda</code></li>\n</ol>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://www.coursera.org/learn/machine-learning\">Coursera’s Machine Learning Course</a></p>\n<h3>Sources</h3>\n<ol>\n<li><a href=\"https://www.coursera.org/learn/machine-learning\">Ng, Andrew. “Machine Learning”. <em>Coursera</em> Accessed January 29, 2018</a></li>\n</ol>","fields":{"slug":"/machine-learning/principles/overfitting-has-many-faces/"},"frontmatter":{"title":"Overfitting Has Many Faces"}}},"pathContext":{"slug":"/machine-learning/principles/overfitting-has-many-faces/"}}

